import React, { Component } from 'react';
import Tp from 'tp-eosjs';
import { LocaleProvider, Layout, Icon, Menu, Dropdown, Row, Col, Button, Message, Divider, Spin} from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import header_bg from './header_bg.png';
import './App.css';

const { Header, Content } = Layout;

const contract_account  = 'eosonebetter';
const voter_table_name  = 'unvoters';
const seconds_per_day   = 24 * 3600;
const seconds_per_month = 30*24*3600;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      walletList: [],
      walletSelect: ' 请选择账号 ',
      addressSelect: '',
      staked_asset: '请先选择账号',
      staked_time: '请先选择账号',
      last_fee_draw_time: '请先选择账号',
      last_lock_draw_time: '请先选择账号',
      fee_draw_asset: '请先选择账号',
      lock_draw_asset: '请先选择账号',
      account_now_staked: '请先选择账号',
      vote_btn_status: '请先选择账号',
      loading: false,
    }

    this.fnTimeTrans = this.fnTimeTrans.bind(this);
    this.checkTime = this.checkTime.bind(this);
    this.displayTimeDelta = this.displayTimeDelta.bind(this);

    this.fnGetWalletList = this.fnGetWalletList.bind(this);
    this.fnWalletChange = this.fnWalletChange.bind(this);
    this.fnCheckAccountVote = this.fnCheckAccountVote.bind(this);
    this.fnGetAcoountNowStaked = this.fnGetAcoountNowStaked.bind(this);
    this.fnTokenWithDraw = this.fnTokenWithDraw.bind(this);
    this.fnVoteProxy = this.fnVoteProxy.bind(this);
    this.fnCheckWithDraw = this.fnCheckWithDraw.bind(this);
  }

  fnTimeTrans = (seconds) => {
    var date = new Date(seconds*1000);
    var Y = date.getFullYear() + '/';
    var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
    var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
    var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
    var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
    var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
    return Y+M+D+h+m+s;
  }

  checkTime = (i) => {
    if( i < 10 ) {i="0"+i; } return i;
  }

  displayTimeDelta = (seconds) => {
    var hour = parseInt(seconds/60/60, 10);
    var minute = parseInt(seconds/60%60, 10);
    var second = parseInt(seconds%60, 10);
    hour = this.checkTime(hour);
    minute = this.checkTime(minute);
    second = this.checkTime(second);
    return hour + ":" + minute + ":" + second;
  }

  fnGetWalletList = () => {
    Tp.getWalletList('eos').then(obj => {
      const eosWallets = [];
      for (let wallet of obj.wallets.eos) {
        eosWallets.push(<Menu.Item key={wallet.address}>{wallet.name}</Menu.Item>);
      }
      this.setState({ walletList: eosWallets });
    }).catch(e => {
      console.error(e);
    })
  }

  fnWalletChange = (value) => {
    this.setState({ loading: true });
    const account_name = value.item.props.children;
    const account_address = value.item.props.eventKey;
    if ( account_name.length === 12 && account_address.length === 53 ) {

      this.setState({ walletSelect: account_name });
      this.setState({ addressSelect: account_address });

      this.fnGetAcoountNowStaked(account_name);
      this.fnCheckAccountVote(account_name);
      this.fnCheckWithDraw();
    } else {
      Message.info("请选择有效的EOS账号");
    }
  }

  fnCheckAccountVote = (voter_name) => {
    Tp.getTableRows({
      json: true,
      code: contract_account,
      scope: contract_account,
      table: voter_table_name,
      lower_bound: voter_name,
      limit: 1
    }).then(obj => {
      if ( obj.data.rows.length === 0 ) {
        this.setState({ staked_asset: "请先委托" });
        this.setState({ staked_time: "请先委托" });
        this.setState({ last_fee_draw_time: "请先委托" });
        this.setState({ last_lock_draw_time: "请先委托" });
        this.setState({ fee_draw_asset: "请先委托" });
        this.setState({ lock_draw_asset: "请先委托" });
      } else {
        if ( obj.data.rows[0].owner === voter_name ) {
          const now_seconds = Date.parse(new Date()) / 1000;

          const staked_amount = obj.data.rows[0].staked_amount;
          const staked_time = obj.data.rows[0].staked_time;
          const last_fee_draw_time = obj.data.rows[0].last_fee_draw_time;
          const last_lock_draw_time = obj.data.rows[0].last_lock_draw_time;
          this.setState({ staked_asset: (staked_amount / 10000.0).toFixed(4) + ' EOS' });
          this.setState({ staked_time: this.fnTimeTrans(staked_time) });
          if ( staked_time === last_fee_draw_time ) {
            this.setState({ last_fee_draw_time: '未曾发放' });
          } else {
            this.setState({ last_fee_draw_time: this.fnTimeTrans(last_fee_draw_time) });
          }
          if ( staked_time === last_lock_draw_time ) {
            this.setState({ last_lock_draw_time: '未曾发放' });
          } else {
            this.setState({ last_lock_draw_time: this.fnTimeTrans(last_lock_draw_time) });
          }

          let days = Math.floor( (now_seconds - staked_time) / seconds_per_day ) - Math.floor( (last_fee_draw_time - staked_time) / seconds_per_day );
          if ( days >= 1 ) {
            this.setState({ fee_draw_asset: (days * staked_amount * 10 / 1000 / 10000.0).toFixed(4) + ' EOSONE' });
          } else {
            this.setState({ fee_draw_asset: "当前时间未可发" });
          }
          let months = Math.floor( (now_seconds - staked_time) / seconds_per_month ) - Math.floor( (last_lock_draw_time - staked_time) / seconds_per_month );
          if ( months >= 1 ) {
            this.setState({ lock_draw_asset: (months * staked_amount * 5 / 12 / 10000.0).toFixed(4) + ' EOSONE' });
          } else {
            this.setState({ lock_draw_asset: "当前时间未可发" });
          }
        } else {
          this.setState({ staked_asset: "请先委托" });
          this.setState({ staked_time: "请先委托" });
          this.setState({ last_fee_draw_time: "请先委托" });
          this.setState({ last_lock_draw_time: "请先委托" });
          this.setState({ fee_draw_asset: "请先委托" });
          this.setState({ lock_draw_asset: "请先委托" });
        }
      }
    });
    this.setState({ loading: false });
  }

  fnCheckWithDraw() {
    let voter_name = this.state.walletSelect;
    Tp.getTableRows({
      json: true,
      code: contract_account,
      scope: contract_account,
      table: voter_table_name,
      lower_bound: voter_name,
      limit: 1
    }).then(obj => {
      if ( obj.data.rows.length === 0 ) {
        this.setState({ vote_btn_status: '请先委托' });
      } else if ( obj.data.rows[0].owner === voter_name ) {
        const staked_time = obj.data.rows[0].staked_time;
        const last_fee_draw_time = obj.data.rows[0].last_fee_draw_time;

        const now_seconds = Date.parse(new Date()) / 1000;
        const last_fee_draw_days = Math.floor( (last_fee_draw_time - staked_time) / seconds_per_day );

        if ( now_seconds >= (last_fee_draw_days + 1) * seconds_per_day + staked_time ) {
          // this.setState({ vote_btn_status: '发放代币' });
          // this.fnTokenWithDraw();
          // return;
        } else {
          let delta_seconds = (last_fee_draw_days + 1) * seconds_per_day + staked_time - now_seconds;
          let delta_time = this.displayTimeDelta(delta_seconds);
          this.setState({ vote_btn_status: delta_time + ' 后发放' });
        }
      } else {
        this.setState({ vote_btn_status: '请先委托' });
      }
    });
    setTimeout(this.fnCheckWithDraw, 1000);
  }

  fnGetAcoountNowStaked = (voter_name) => {
    Tp.getTableRows({
      json: true,
      code: 'eosio',
      scope: 'eosio',
      table: 'voters',
      lower_bound: voter_name,
      limit: 1
    }).then(obj => {
      if ( obj.data.rows.length === 0 ) {
        this.setState({ account_now_staked: '请先抵押' });
      } else if ( obj.data.rows[0].owner === voter_name ) {
        this.setState({ account_now_staked: (obj.data.rows[0].staked / 10000.0).toFixed(4) + ' EOS' });
      } else {
        this.setState({ account_now_staked: '请先抵押' });
      }
    }).catch(e => {
      console.error(e);
    });
  }

  fnTokenWithDraw = () => {
    if (this.state.walletSelect.length === 12 && this.state.addressSelect.length === 53) {
      Tp.pushEosAction({
        actions: [{
          account: contract_account,
          name: 'withdraw',
          authorization: [{
            actor: this.state.walletSelect,
            permission: 'active'
          }],
          data: {
            voter_name: this.state.walletSelect
          }
        }],
        address: this.state.addressSelect,
        account: this.state.walletSelect
      }).then(obj => {
        if ( obj.result ) {
          this.fnCheckAccountVote(this.state.walletSelect);
          setTimeout(this.fnCheckAccountVote(this.state.walletSelect), 1000);
          this.fnCheckWithDraw();
          Message.success("发放成功");
        } else {
          // Message.error("操作失败");
        }
      }).catch(e => console.error(e));
    } else {
      Message.info("请先选择有效的EOS账号");
    }
  }

  fnVoteProxy = () => {
    if (this.state.walletSelect.length === 12 && this.state.addressSelect.length === 53) {
      Tp.pushEosAction({
        actions: [{
          account: 'eosio',
          name: 'voteproducer',
          authorization: [{
            actor: this.state.walletSelect,
            permission: 'active'
          }],
          data: {
            voter: this.state.walletSelect,
            proxy: contract_account,
            producers: ''
          }
        }],
        address: this.state.addressSelect,
        account: this.state.walletSelect
      }).then(obj => {
        if ( obj.result ) {
          this.fnCheckAccountVote(this.state.walletSelect);
          Message.success("委托成功");
        } else {
          // Message.error("操作失败");
        }
      }).catch(e => console.error(e));
    } else {
      Message.info("请先选择有效的EOS账号");
    }
  }

  componentWillMount() {
    this.fnGetWalletList();
  }
  componentDidMount() {
    //
  }

  render() {

    const menu = (
      <Menu onClick={this.fnWalletChange}>
        {this.state.walletList}
      </Menu>
    );

    const container = (
      <Layout style={{ background: '#0B1B41' }}>
      <Header style={{ background: 'url('+header_bg+')', backgroundSize: '100% 100%', position: 'fixed', zIndex: 1, width: '100%' }}></Header>
      <div style={{ padding: '140px 0 10px 0', textAlign: 'center' }}>
        <Dropdown overlay={menu} trigger={['click']}>
          <a className="ant-dropdown-link" href="#" style={{ fontSize: '14px', color: '#2760E7' }}>
            <Icon type="user"/>&nbsp;&nbsp;{this.state.walletSelect}&nbsp;&nbsp;<Icon type="down"/>
          </a>
        </Dropdown>
      </div>
      <Content style={{ margin: '0 5% 20px 5%' }}>
        <Divider style={{ margin: '10px 0 10px 0', background: '#1c367f' }}></Divider>
        <Row style={{ textAlign: 'left', margin: '24px 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '14px', color: '#a5b6e7' }}>
          <Col span={24}>
            <p>抵押数量： {this.state.account_now_staked}</p>
          </Col>
        </Row>
        <Row style={{ textAlign: 'left', margin: '0 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '14px', color: '#a5b6e7' }}>
          <Col span={24}>
            <p>投票时间： {this.state.staked_time}</p>
          </Col>
        </Row>
        <Row style={{ textAlign: 'left', margin: '0 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '14px', color: '#a5b6e7' }}>
          <Col span={24}>
            <p>利息上次发放： {this.state.last_fee_draw_time}</p>
          </Col>
        </Row>
        <Row style={{ textAlign: 'left', margin: '0 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '14px', color: '#a5b6e7' }}>
          <Col span={24}>
            <p>锁仓上次发放： {this.state.last_lock_draw_time}</p>
          </Col>
        </Row>
        <Divider style={{ margin: '15px 0 15px 0', background: '#1c367f' }}></Divider>
        <Row style={{ textAlign: 'left', margin: '0 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '14px', color: '#a5b6e7' }}>
          <Col span={24}>
            <p>本次可发利息： {this.state.fee_draw_asset}</p>
          </Col>
        </Row>
        <Row style={{ textAlign: 'left', margin: '0 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '14px', color: '#a5b6e7' }}>
          <Col span={24}>
            <p>本次可发锁仓： {this.state.lock_draw_asset}</p>
          </Col>
        </Row>
        <Row style={{ textAlign: 'center', margin: '10px 0 10px 0', padding: '0 0 0 0', width: '100%' }}>
          <Col span={24}>
            <Button type="primary" style={{ width: '100%', fontSize: '16px', padding: '10px 0 35px 0', borderRadius: "25px" }} disabled onClick={this.fnTokenWithDraw}>{this.state.vote_btn_status}</Button>
            {/* {
              this.state.fee_draw_asset.indexOf(' EOSONE') > 0 || this.state.lock_draw_asset.indexOf(' EOSONE') > 0 ?
              <Button type="primary" style={{ width: '100%', fontSize: '16px', padding: '10px 0 35px 0', background: '#267AF3', borderRadius: "25px" }} onClick={this.fnTokenWithDraw}>{this.state.vote_btn_status}</Button> :
              <Button type="primary" style={{ width: '100%', fontSize: '16px', padding: '10px 0 35px 0', borderRadius: "25px" }} disabled onClick={this.fnTokenWithDraw}>{this.state.vote_btn_status}</Button>
            } */}
          </Col>
        </Row>
        <Row style={{ textAlign: 'center', margin: '10 0 10px 0', padding: '0 0 0 0', width: '100%' }}>
          <Col span={24}>
            <Button style={{ width: '100%', fontSize: '16px', padding: '10px 0 35px 0', borderColor: "#1E88FF", background: '#0B1B41', color: '#297FF4', borderRadius: "25px" }} onClick={this.fnVoteProxy}>立即委托</Button>
          </Col>
        </Row>
        <Row style={{ textAlign: 'center', margin: '0 0 0 0', padding: '0 0 0 0', width: '100%', fontSize: '12px', color: '#a5b6e7' }}>
          <Col span={24}>
            <h3 style={{textAlign: 'left', margin: '20px 0 0 0', color: '#a5b6e7'}}>智能合约方案：</h3>
            <p style={{textAlign: 'left', margin: '10px 0 0 0'}}>1、按EOS比例 1:5 赠送EOSONE代币，赠送的代币按12个月，每个月分批释放；</p>
            <p style={{textAlign: 'left', margin: '0 0 0 0'}}>2、每天按千分之10利息发放代币；</p>
            <p style={{textAlign: 'left', margin: '0 0 0 0'}}>3、利息相邻两次发放间隔不得小于1天、锁仓相邻两次发放间隔不得小于1个月；</p>
            <p style={{textAlign: 'left', margin: '0 0 0 0'}}>4、修改抵押或修改投票，如果满足第3条合约将自动发放修改前应得代币（合约每小时扫描一次每个投票状态，所以修改后可能不会马上发放）；</p>
            <p style={{textAlign: 'left', margin: '0 0 0 0'}}>5、授权满1年重新按 1:5 赠送代币。</p>
            <p style={{textAlign: 'left', margin: '0 0 0 0'}}>例：某大户10万EOS投权给我们并存满6个月，可获得：</p>
            <p style={{textAlign: 'left', margin: '0 0 0 0'}}>赠送25万代币(10w * 5 / 12 * 6) + 18万利息(10w * 10‰ * 30 * 6)，共可获得43万代币</p>
            <p></p>
            <p style={{textAlign: 'left', margin: '0 0 10px 0'}}>客服微信: luozhichujian</p>
          </Col>
        </Row>
      </Content>
      </Layout>
    );

    return (
      <div>
        <LocaleProvider locale={zh_CN}>
          <Spin size="large" spinning={this.state.loading}>{container}</Spin>
        </LocaleProvider>
      </div>
    );
  }
}

export default App;



